import React, { Component } from 'react';
import { Button, Col, Grid, Image, Row } from 'react-bootstrap';
import JsBarcode from 'jsbarcode';
import Quagga from 'quagga';

import logo from './logo.svg';
import './App.css';


class Scanner extends Component {

    render() {
        return (
            <div id="interactive" className="viewport"/>
        );
    }

    componentDidMount() {
        Quagga.init({
            inputStream: {
                type : "LiveStream",
                constraints: {
                    width: 730,
                    height: 500,
                    facing: "environment" // or user
                }
            },
            locator: {
                patchSize: "medium",
                halfSample: true
            },
            numOfWorkers: 2,
            decoder: {
                readers : [ "code_128_reader"]
            },
            locate: true
        }, function(err) {
            if (err) {
                return console.log(err);
            }
            Quagga.start();
        });
        Quagga.onDetected(this._onDetected);
    }

    componentWillUnmount() {
        Quagga.offDetected(this._onDetected);
    }

    _onDetected(result) {
        this.props.onDetected(result);
    }
};



class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      scanning: false,
      results: [],
    }
  }

  componentDidMount() {
    JsBarcode(this.refs.barcode, "http://www.gap.com/browse/product.do?cid=92113&pid=910166002");
  }

  render() {
    return (
      <div className="App">
        <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/latest/css/bootstrap.min.css" />

        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>

        { this.state.scanning ? <Scanner /> : null }

        <div className="box">
          <h2>Welcome</h2>
          <center>
            <Button bsSize="large" onClick={() => { this.setState({scanning: !this.state.scanning}); }}>Scan Barcode</Button>
          </center>
        </div>

        <div className="box">
          <h2>Your Pickup</h2>
          <center>
            <canvas ref="barcode" />
          </center>
        </div>

        <div className="box">
          <h2>For You</h2>
          <Grid>
            <Row>
              <Col xs={4} md={4}>
                <Image src="http://via.placeholder.com/150x150" thumbnail />
              </Col>
              <Col xs={4} md={4}>
                <Image src="http://via.placeholder.com/150x150" thumbnail />
              </Col>
              <Col xs={4} md={4}>
                <Image src="http://via.placeholder.com/150x150" thumbnail />
              </Col>
            </Row>
          </Grid>


        </div>

        <div className="box">
          <h2>Catalog</h2>
        </div>

      </div>
    );
  }
}

export default App;



